import React from 'react'
import '../assets/css/password.css';
import {Toast, Button, WingBlank, InputItem, WhiteSpace, List, Picker } from 'antd-mobile';
import axios from 'axios'

const Item = List.Item;

class Password extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            sn: '',
            pwdlist: [],
            hasData: false,
            signal_id: [0],
            signalOptions: [
                {
                    value: 0,
                    label: '0.5小时'
                }, {
                    value: 1,
                    label: '1小时'
                }, {
                    value: 2,
                    label: '2小时'
                }, {
                    value: 3,
                    label: '3小时'
                }, {
                    value: 4,
                    label: '4小时'
                }, {
                    value: 5,
                    label: '5小时'
                }, {
                    value: 6,
                    label: '8小时'
                }, {
                    value: 7,
                    label: '12小时'
                }, {
                    value: 8,
                    label: '16小时'
                }, {
                    value: 9,
                    label: '25小时'
                }
            ]
        }

        this.init();
    }

    init(){
        var that = this;
        if(typeof WeixinJSBridge == "undefined"){
            if(document.addEventListener){
                document.addEventListener('WeixinJSBridgeReady', wechat, false);
            }
            else if(document.attachEvent){
                document.attachEvent('WeixinJSBridgeReady', wechat);
                document.attachEvent('onWeixinJSBridgeReady', wechat);
            }
        }
        else{
            wechat();
        }

        function wechat(){
            wx.config({
                debug:false,
                signature: signature,
                appId: appId,
                timestamp: timeStamp,
                nonceStr: nonceStr,
                jsApiList: ['scanQRCode'],
            });
            wx.ready(function(){

            });

            wx.error(function(res){
                Toast.fail('启动失败', 2);
            });
        }
    }

    scan(){
        var that = this;
        wx.scanQRCode({
            needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
            scanType: ["qrCode"], // 可以指定扫二维码还是一维码，默认二者都有
            success: function (res) {
                var result = res.resultStr; // 当needResult 为 1 时，扫码返回的结果
                var index = result.indexOf('?');
                var second_index = index + 1;
                var sn_str = result.substring(second_index);
                var sn_arr =sn_str.split('=');

                that.setState({
                    sn: sn_arr[1]
                });
            }
        });
    }

    chgsn(value){
        this.setState({
            sn: value
        });
    }

    submit(){
        if(!this.state.sn){
            Toast.fail('请扫描设备', 2);
            return false;
        }

        if(!this.state.loading) {
            Toast.loading('提交中…', 30);
            this.setState({
                loading: true
            });

            axios({
                method: 'post',
                url: '/devices/pwdlist',
                data: {
                    sn: this.state.sn,
                    signal_id: this.state.signal_id[0]
                }
            }).then(function (response) {
                if (response.status == 200 && response.data.code == 0) {
                    Toast.hide();
                    this.setState({
                        loading: false,
                        hasData: true,
                        pwdlist: response.data.data.pwdlist
                    });
                }
                else {
                    this.setState({
                        loading: false
                    });
                    Toast.fail(response.data.message, 2);
                }
            }.bind(this)).catch(function (error) {
                this.setState({
                    loading: false
                });
                Toast.fail('未知错误', 2);
            }.bind(this));
        }
    }

    render() {
        return (
            <div className="d-container">
                <List renderHeader={() => '设备信息'}>
                    <div className="scan-container">
                        <InputItem
                            value={this.state.sn}
                            onChange={this.chgsn.bind(this)}
                            placeholder={'请扫描设备二维码'}
                        >设备SN</InputItem>
                        <svg className="icon scan" ariaHidden="true" onClick={this.scan.bind(this)}>
                            <use xlinkHref="#icon-scan"></use>
                        </svg>
                    </div>
                    <div>
                        <Picker data={this.state.signalOptions} value={this.state.signal_id} onChange={v => this.setState({ signal_id: v })} className="forss" cols={1}>
                            <List.Item arrow="horizontal">充电时长</List.Item>
                        </Picker>
                    </div>
                </List>

                <WhiteSpace size="xl" />
                <WingBlank size="lg">
                    <Button className="btn" type="primary" disabled={this.state.loading || !this.state.sn} loading={this.state.loading} onClick={this.submit.bind(this)}>提交</Button>
                </WingBlank>
                <WhiteSpace size="xl" />
                <ul className="pwdlist">
                    {this.state.pwdlist.map((item, index) => (
                        <li><span className="number">{index + 1}.</span> {item}</li>
                    ))}
                </ul>
            </div>
        );
    }
}

export default Password;



